<template>
  <div class="offcanvas-body">
    <div class="d-flex mx-3" role="search">
      <button @click="searchValue" class="Search btn">
        <i class="fa-solid fa-magnifying-glass mt-2" style="color: #232325"></i>
      </button>
      <input
        class="form-control ps-5 me-2"
        style="border: none; border-radius: 25px; height: 35px; margin: auto"
        type="search"
        placeholder="Search"
        aria-label="Search"
        v-model="searchTar"
        @keyup.enter="searchValue"
      />
    </div>
    <ul
      class="navbar-nav justify-content-center align-items-lg-center flex-grow-1 pe-3"
    >
      <NavbarItemVue
        v-for="(type, index) in typeItems"
        :key="index"
        :typeItme="type"
        @trendProduct="trendProduct"
      />
    </ul>
    <ul
      class="navbar-nav justify-content-start align-items-lg-center flex-grow-1 pe-3"
    >
      <li class="nav-item">
        <button class="btn">
          <i class="fa-regular fa-bell fs-5"></i>
        </button>
      </li>
      <li class="nav-item">
        <button class="btn">
          <i class="fa-regular fa-comments fs-5"></i>
        </button>
      </li>
      <li class="nav-item d-flex justify-content-start">
        <button class="btn">
          <i class="fa-solid fa-house-chimney-window fs-5"></i>
        </button>
      </li>
      <li class="nav-item">
        <button
          class="btn"
          data-bs-toggle="collapse"
          data-bs-target="#cardShopping"
          aria-expanded="false"
          aria-controls="cardShopping"
        >
          <i class="fa-solid fa-cart-shopping fs-5"> </i>
          <span
            v-if="$store.state.listSpecificProducts.length != 0"
            class="position-absolute text-center fw-bold"
            style="
              width: 15px;
              height: 15px;
              border-radius: 50%;
              background-color: red;
              font-size: 8px;
            "
            >{{ $store.state.listSpecificProducts.length }}</span
          >
        </button>
        <CardProductsVue />
      </li>
    </ul>
    <button class="btn">
      <i
        v-if="
          this.$store.state.user.email == '' &&
          this.$store.state.indexLoginUser == null
        "
        class="fa-solid fa-user-tie"
      ></i>
      <img
        v-else
        :src="dataUser.imgUser"
        style="width: 40px; height: 40px; object-fit: cover; border-radius: 50%"
        alt=""
      />
    </button>
  </div>
</template>

<script>
import NavbarItemVue from "./NavbarItem.vue";
import CardProductsVue from "./CardProducts.vue";
export default {
  name: "navbarBody",
  data() {
    return {
      typeItems: ["Home", "Promo", "ContactUs", "Login"],
      searchTar: "",
    };
  },
  components: { NavbarItemVue, CardProductsVue },
  methods: {
    trendProduct(type) {
      this.$emit("trendProduct", type);
    },
    searchValue() {
      this.$emit("searchValue", this.searchTar);
    },
  },
  computed: {
    indexUser() {
      return this.$store.state.indexLoginUser;
    },
    dataUser() {
      if (this.indexUser != null) {
        return this.$store.state.users[this.indexUser];
      } else {
        return this.$store.state.user;
      }
    },
  },
};
</script>

<style scoped>
.form-control:focus {
  box-shadow: 0 0 0 0.25rem rgba(255, 255, 255, 0.215);
}
.Search {
  transform: translate(40px, -1px);
  border: none;
  border-radius: 50%;
}

button:hover {
  color: darkgrey;
}
button {
  color: #fff;
}
.dropdown-toggle::after {
  display: none;
  margin-left: 0em;
  vertical-align: 0em;
  content: "";
  border: none;
}
@media (min-width: 991px) {
  .collapse {
    transform: translateX(-200px);
  }
}
</style>
