<template>
  <nav
    class="navbar navbar-dark navbar-expand-lg fixed-top shadow"
    :class="{navbarDark:mode}"
    style="transition: 0.5s"
  >
    <!-- bg-dark -->
    <div class="container-fluid">
      <a class="navbar-brand"
        ><img
          style="width: 50px; cursor: pointer"
          class="rounded"
          src="../../assets/logo.webp"
      /></a>
      <button
        class="navbar-toggler"
        type="button"
        data-bs-toggle="offcanvas"
        data-bs-target="#offcanvasDarkNavbar"
        aria-controls="offcanvasDarkNavbar"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon"></span>
      </button>
      <div
        class="offcanvas offcanvas-end"
        :class="{offcanvasDark:mode}"
        tabindex="-1"
        id="offcanvasDarkNavbar"
        aria-labelledby="offcanvasDarkNavbarLabel"
      >
        <NavbarHeadVue />
        <NavbarBodyVue @searchValue="search" />
      </div>
    </div>
  </nav>
</template>

<script>
import NavbarHeadVue from "./NavbarHead.vue";
import NavbarBodyVue from "./NavbarBody.vue";
export default {
  name: "navbarMain",
  components: { NavbarHeadVue, NavbarBodyVue },
  methods: {
    // trendProduct(trend) {
    //   this.$emit("trendProduct", trend);
    // },
    search(value) {
      this.$emit("searchValue", value);
    },
  },
 computed:{
  mode() {
      if (this.$store.state.modeSite == "Dark mode") {
        return true;
      } else return false;
    },
 }
};
</script>
<style scoped>
.navbar,.offcanvas{
  background-color: rgb(0, 104, 127)
}
.navbarDark,.offcanvasDark{
  background-color: rgb(0, 42, 51)
}
@media (max-width: 500px) {
  .offcanvas {
    max-width: 300px;
  }
}
</style>
