<template>
  <div
    class="collapse position-absolute end-0"
    style="transition: 0.5s; border: none; background: fixed"
    id="cardShopping"
  >
    <div class="card card-body" style="border: none; background: fixed">
      <div class="card shadow" style="; border: none">
        <div class="card-body">
          <h5 class="card-title">checkout counter</h5>
          <h6
            class="card-subtitle text-end mb-2"
            style="color: rgb(73, 73, 255)"
          >
            view details
          </h6>

          <div
            class="card overflow-y-auto"
            v-if="listSpecificProducts.length != 0"
            style="max-height: 250px"
          >
            <ul class="list-group list-group-flush">
              <li
                v-for="(product, index) in listSpecificProducts"
                :key="index"
                class="list-group-item d-flex justify-content-between align-items-center p-1"
              >
                <img
                  @click="ClickProduct(product)"
                  class="btn p-0"
                  data-bs-toggle="modal"
                  data-bs-target="#exampleModal"
                  :src="product.previewURL"
                  style="
                    width: 50px;
                    height: 70px;
                    border-radius: 7px;
                    object-fit: cover;
                    margin-right: 3px;
                    background-color: #eee;
                    border-right: solid rgba(162, 154, 154, 0.663);
                  "
                />
                <div style="font-size: 12px">
                  {{ shortenText(product.tags) }}...
                </div>
                <span
                  class="mx-1"
                  style="
                    text-shadow: 0px 0px 1px rgba(0, 0, 255, 0.755);
                    font-size: 15px;
                    color: rgba(0, 0, 255, 0.826);
                    font-weight: bold;
                  "
                  >${{ product.price }}</span
                >
                <button
                  @click="removeProduct(product)"
                  class="btn btn-primary"
                  style="font-size: 10px"
                >
                  remove
                </button>
              </li>
            </ul>
          </div>
          <p v-else style="color: rgba(222, 31, 31, 0.747)">
            There are no products
          </p>
          <!--   webformatURL: String,
      title: String,
      googleUrl: String,
      price: Number, -->
          <button
            data-bs-toggle="collapse"
            data-bs-target="#cardShopping"
            class="btn btn-primary w-75 mx-auto mt-2"
          >
            Card link
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CardGroupSingleProduct from "./CardGroupSingleProduct.vue";
export default {
  name: "caedProducts",
  methods: {
    shortenText(text) {
      const title = text.slice(0, 26);
      return title;
    },
    removeProduct(prodact) {
      this.$store.state.listSpecificProducts =
        this.$store.state.listSpecificProducts.filter(
          (object) => object != prodact
        );
    },
    ClickProduct(prodact) {
      const index = this.listSpecificProducts.findIndex((x) => x == prodact);
      this.$store.state.indexDetailsProduct = index;
    },
  },
  computed: {
    listSpecificProducts() {
      return this.$store.state.listSpecificProducts;
    },
  },
  components: {},
};
</script>

<style scoped>
::-webkit-scrollbar {
  width: 3px;
}
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 3px grey;
  border-radius: 50px;
}

::-webkit-scrollbar-thumb {
  background: rgb(73, 73, 255);
  border-radius: 50px;
}
.shadow {
  width: 17rem
}
@media (max-width:500px) {
  .collapse {
    right:0%;
    transform:translate(15px,-176px);
    padding: 0%;
  }
.shadow {
  width: 14.5rem;
}
::-webkit-scrollbar {
  width: 1px;
}
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 1px grey;
  border-radius: 100px;
}

::-webkit-scrollbar-thumb {
  background: rgb(73, 73, 255);
  border-radius: 100px;
}
}
</style>
