<template>
  <li class="nav-item">
    <button
      @click="clickHandler"
      class="nav-link px-3"
      :class="classItype"
      aria-current="page"
      ref="login"
    >
      <!-- TODO:update ref -->
      {{ typeItme }}
    </button>
  </li>
</template>

<script>
export default {
  name: "navItem",
  props: {
    typeItme: String,
  },
  data() {
    return {
      classItype: "",
    };
  },
  methods: {
    checkItem() {
      if (this.typeItme == "Login") {
        this.classItype = "btn fw-bold";
        if (
          this.$store.state.indexLoginUser != null ||
          this.$store.state.user.email != ""
        ) {
          this.$refs.login.setAttribute("disabled", "true");
        }
      }
    },
    clickHandler() {
      scrollTo(0, 0);
      if (this.typeItme == "Home") {
        this.$router.push("/");
      } else if (this.typeItme == "Login") {
        this.$router.push("/login");
      } else if (this.typeItme == "Promo") {
        this.$router.push("/promo");
      }
    },
  },
  mounted() {
    this.checkItem();
  },
};
</script>
